@import "../../App.scss";
.heading{
    text-align: left !important;
    font-family: $secondary-font !important;
    font-size: 3em;
    font-weight: 500;
}
.category-section{
    
   form{
       width: 40%;
    .textfield{
        margin: 2% 0;
    }
    .btn{
        margin-top: 2%;
    }
   }
   .category-row{
       background-color: #f7f7f7;
       text-align: center;
       padding: 1% 3%;
       margin: 2% 10%;
       border-radius: 10px;
       .category-item{
           padding: 1px;
           .name{
               font-weight: bold;
           }
       }
   }
}