@import "../../App.scss";



.product-detail{
    h2{
        padding-top: 1% !important;
    }
    .emi-option{
        border: 1px solid #cccf;
        border-radius: 10px;
        padding: 1% 2%;
        margin-left: 2%;
    }
    .attr-cont{
        background-color: #f7f7f786;
        margin: 1% 1% !important;
        padding: 1%;
        border-radius: 10px;
        .attr-key{
            p{
                font-weight: 600;
            }
        }
    }

    .spec-cont{
        .key-cont{
            background-color: #f7f7f7;
            padding: 2%;
        }
    }

    .varation-section{
        margin: 4% 0 !important;
        .singlevar{
            background-color: #f7f7f7;
            margin: 2% 0 !important;
            border-radius: 10px;
            padding:5px;
           .singlevaritem{
            .key{
                font-weight: 600 !important; 
            }
           }
        }
    }

    .variation-imgs{
        margin: 3%  0  !important;
        img{
            width: 50% !important; 
        }
    }

}