@import "../../App.scss";


.create-product-section{
    h3{
        margin-top: 5%;
        margin-bottom: 3%;
    }
 form{
     margin: 2% 10%;
     .textfield{
         margin: 1.5% 0;
     }
     .emi-cont{
         margin: 2% !important;
         border: 1px solid rgb(243, 243, 243);
         border-radius: 10px;
         padding: 2% 4%;
         
     }
     .key-value-container{
         background-color: #f7f7f7;
         border-radius: 10px;
         padding: 1%;
         .key{
             font-weight: 600;
         }
         .value{
             padding-left: 5px;
         }
         .delete{
             text-align: right;
         }
     }

     .specification-div{
         background-color: #f7f7f7;
         padding: 2%;
         .row{
            div{
                padding: 1% 2%;
                margin: 1% 0;
            }
            .key-cont{
                background-color: #ebebebab;
                p{
                    font-weight: 600;
                }
            }
         }
     }

     .variation-cont{
         background-color: #f7f7f7;
         padding: 1%;
         border-radius: 10px;
         margin-bottom: 10% !important;
         .variation-row{
             background-color: rgba(224, 224, 224, 0.486);
             margin: 2% !important;
             border-radius: 10px;
             padding:5px;
             .key{
                 font-weight: bold;
             }
         }
     }
 }   
}